@import url("../../assets/styles/color.module.css");
@import url("../../assets/styles/animation.module.css");
@import url("../../assets/styles/fonts.module.css");

.download {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 210px;
}

.left {
    margin-left: 92px;
}

.left__circle {
    position: relative;
    width: 508px;
    height: 508px;
    border-radius: 50%;
    background-color: var(--c-white);
}

.left__stikerIphone {
    position: absolute;
    width: 320px;
    z-index: 2;
    top: -41px;
    left: 20px;
    animation: stikerIphone 5s ease-in-out infinite;
}

.left__stikerAndroid {
    width: 300px;
    top: -2px;
    right: 21px;
    position: absolute;
    animation: stikerAndroid 5s ease-in-out infinite;
}

.right {
    width: 583px;
    margin-right: 121px;
}

.right__title {
    font-family: Manrope;
    font-size: 40px;
    line-height: 52px;
    color: var(--c-black-blue);
    margin-bottom: 24px;
}

.right__text {
    font-family: ManropeText;
    font-size: 20px;
    line-height: 32px;
    color: var(--c-black-blue);
    margin-bottom: 40px;
}

.right__links {
    display: grid;
    grid-row: 50% 50%;
}

.right__googlePlay {
    width: 162px;
}

.right__appStore {
    width: 144px;
}

@keyframes stikerIphone {
    from {
        top: -41px;
    }

    50% {
        top: -47px;
    }

    to {
        top: -41px;
    }
}

@keyframes stikerAndroid {
    from {
        top: -2px;
    }

    50% {
        top: 4px;
    }

    to {
        top: -2px;
    }
}