@import url("../../assets/styles/color.module.css");
@import url("../../assets/styles/fonts.module.css");

.builder {
    height: 616px;
    background-color: var(--c-white);
    border-radius: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 168px;
}

.left {
    display: flex;
    flex-direction: column;
    margin-left: 80px;
    max-width: 516px;
}

.left__title {
    font-family: Manrope;
    font-size: 40px;
    line-height: 52px;
    margin-bottom: 24px;
}

.left__text {
    font-family: ManropeText;
    font-size: 20px;
    line-height: 32px;
}

.constructor {
    max-width: 812px;
}