@import url("../../assets/styles/color.module.css");
@import url("../../assets/styles/animation.module.css");
@import url("../../assets/styles/fonts.module.css");

.yourApp {
    background-color: var(--c-brand);
    border-radius: 40px;
    height: 616px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 161px;
    position: relative;
}

.yourApp__azure {
    position: absolute;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background: url("../../assets/img/faces/azure.png"), var(--c-twisted-blue);
    background-size: 60px;
    background-position: bottom center;
    background-repeat: no-repeat;
    top: -32px;
    right: 152px;
}

.yourApp__bang {
    position: absolute;
    width: 120px;
    height: 120px;
    border-radius: 50%;
    background: url("../../assets/img/faces/bang.png"), var(--c-pink);
    background-size: 75px;
    background-position: bottom center;
    background-repeat: no-repeat;
    top: 16px;
    right: 24px;
    z-index: 5;
}

.left {
    width: 624px;
    margin-left: 80px;
    color: var(--c-white);
}

.left__title {
    font-family: Manrope;
    font-size: 40px;
    line-height: 52px;
    letter-spacing: -0.02em;
    margin-bottom: 24px;
}

.left__text {
    font-family: ManropeText;
    font-size: 20px;
    line-height: 32px;
    margin-bottom: 47px;
}

.left__button {
    position: relative;
    color: var(--c-brand);
    padding: 7px 20px 9px 0px;
    border-radius: 12px;
    font-family: Manrope;
    font-size: 20px;
    line-height: 32px;
    background-color: var(--c-white);
    width: 191px;
    transition: .5s;
    margin-left: 100%;
    transform: translate(-100%, 0);
}

.left__button::before {
    content: "";
    width: 15px;
    height: 2px;
    position: absolute;
    top: 50%;
    transform: translate( 0, -50%);
    right: 20px;
    background-color: var(--c-brand);
}

.left__button::after {
    content: "";
    position: absolute;
    width: 7px;
    height: 7px;
    border-left: 2px solid var(--c-brand);
    border-bottom: 2px solid var(--c-brand);
    transform: rotate(225deg) translate( 0, 50%);
    top: 48%;
    right: 23px;
}

.left__button:hover {
    opacity: 0.7;
    transition: .5s;
}

.right {
    height: 616px;
    width: 728px;
    overflow: hidden;
    position: relative;
}

.right__laptop {
    position: absolute;
    width: 826px;
    height: 478px;
    top: 93px;
    right: -288px;
}

.right__iphoneTwitter {
    position: absolute;
    width: 246px;
    height: 496px;
    bottom: -64px;
    right: 386px;
}

.yourApp__pink {
    position: absolute;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background: url("../../assets/img/faces/pink.png"), var(--c-vanilla);
    background-size: 60px;
    background-position: bottom center;
    background-repeat: no-repeat;
    bottom: 40px;
    right: 581px;
    z-index: 5;
}

.yourApp__afro {
    position: absolute;
    width: 120px;
    height: 120px;
    border-radius: 50%;
    background: url("../../assets/img/faces/afro.png"), var(--c-twisted-blue);
    background-size: 95px;
    background-position: bottom center;
    background-repeat: no-repeat;
    bottom: -40px;
    left: 643px;
    z-index: 5;
}