@import url("../../assets/styles/color.module.css");
@import url("../../assets/styles/animation.module.css");
@import url("../../assets/styles/fonts.module.css");

.look {
    height: 440px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 168px;
}

.left {
    width: 440px;
    height: 440px;
}

.left__avatar {
    width: 440px;
    height: 440px;
    margin-left: 144px;
    background-repeat: no-repeat;
    position: relative;
    border-radius: 50%;
    animation: look var(--lookAni) step-start infinite;
}

.left__avatar::after {
    content: "";
    padding: 8px 16px;
    border-radius: 12px;
    font-family: Manrope;
    font-size: 32px;
    line-height: 40px;
    position: absolute;
    color: var(--c-white);
    animation: la var(--lookAni) infinite step-start;
}

@keyframes la {
    16.5% {
        top: 6px;
        right: -47px;
        content: "@Isabelle";
        background-color: var(--c-pink);
    }

    33% {
        top: 361px;
        right: 320px;
        content: "@Nathan";
        background-color: var(--c-brown);
    }

    49.5% {
        top: 19px;
        right: 307px;
        content: "@vikkki";
        background-color: var(--c-tech-wave);
    }

    66% {
        top: 21px;
        right: -18px;
        content: "@dmtrrr";
        background-color: var(--c-cyan);
    }

    84.5% {
        top: 192px;
        right: 374px;
        content: "@m3rii";
        background-color: var(--c-twisted-blue);
    }

    100% {
        top: -21px;
        right: 275px;
        content: "@ressst";
        background-color: var(--c-creole);
    }
}

@keyframes look {

    16.5% {
        background: url("../../assets/img/faces/pink.png"), var(--c-brand);
        background-position: center bottom;
        background-size: 72%;
        background-repeat: no-repeat;
    }

    33% {
        background: url("../../assets/img/faces/ponytail.png"), var(--c-cyan);
        background-position: center bottom;
        background-size: 60%;
        background-repeat: no-repeat;
    }

    49.5% {
        background: url("../../assets/img/faces/glasses.png"), var(--c-pink);
        background-position: center bottom;
        background-size: 60%;
        background-repeat: no-repeat;
    }

    66% {
        background: url("../../assets/img/faces/redPoint.png"), var(--c-pantone);
        background-position: center bottom;
        background-size: 60%;
        background-repeat: no-repeat;
    }

    84.5% {
        background: url("../../assets/img/faces/bang.png"), var(--c-purple-light);
        background-position: center bottom;
        background-size: 60%;
        background-repeat: no-repeat;
    }

    100% {
        background: url("../../assets/img/faces/afro.png"), var(--c-vanilla);
        background-position: center bottom;
        background-size: 75%;
        background-repeat: no-repeat;
    }
}

.right {
    width: 583px;
    margin-right: 142px;
}

.right__title {
    font-family: Manrope;
    font-size: 40px;
    line-height: 52px;
    margin-bottom: 24px;
    position: relative;
}

.right__title::after {
    content: "Coming Soon";
    font-family: Manrope;
    font-size: 16px;
    line-height: 16px;
    color: var(--c-white);
    border-radius: 6px;
    padding: 5px 8px 7px;
    background-color: var(--c-brand);
    position: absolute;
    left: 314px;
    top: 13px;
}

.right__text {
    font-family: ManropeText;
    font-size: 20px;
    line-height: 32px;
}