@import url("../../assets/styles/color.module.css");
@import url("../../assets/styles/animation.module.css");
@import url("../../assets/styles/fonts.module.css");

.loadModal {
    z-index: 9;
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background-color: var(--c-white);
    display: none;
    transition: .5s;
}

.loadModalAvatar {
    display: none;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-repeat: no-repeat;
    background-position: center bottom;
    width: 120px;
    height: 120px;
    z-index: 10;
    transition: .5s;
    animation: load 1.5s step-end forwards;
    border-radius: 50%;
    border: 5px solid var(--c-brand);
}

@keyframes load {
    0% {
        background-image: url("../../assets/img/faces/pink.png");
        background-size: 70%;
    }

    7% {
        background-image: url("../../assets/img/faces/bang.png");
        background-size: 60%;
    }

    14% {
        background-image: url("../../assets/img/faces/redPoint.png");
        background-size: 60%;
    }

    22% {
        background-image: url("../../assets/img/faces/ponytail.png");
        background-size: 60%;
    }

    29%, 36%, 48% {
        background-image: url("../../assets/img/faces/glasses.png");
        background-size: 65%;
        width: 120px;
        height: 120px;
        animation-timing-function: ease-in-out;
    }

    60%, 80% {
        background-image: url("../../assets/img/faces/glasses.png");
        background-size: 65%;
        width: 200px;
        height: 200px;
        animation-timing-function: ease-in-out;
        
        border: 0px solid var(--c-brand);
    }

    100% {
        background-image: url("../../assets/img/faces/glasses.png");
        animation-timing-function: ease-in-out;
        background-size: 65%;
        width: 120px;
        height: 120px;
        border: 0px solid var(--c-brand);
    }
}

.hero {
    padding-top: 232px;
}

.heroText {
    color: var(--c-black-blue);
    width: 942px;
    margin: 0 auto 84px auto;
}

.heroText__title {
    font-family: Manrope;
    font-size: 56px;
    line-height: 72px;
    letter-spacing: -2px;
    text-align: center;
    margin-bottom: 24px;
    animation: heroTitleLoad ease-in-out var(--heroTitle);
}

@keyframes heroTitleLoad {
    from, 50% {
        animation-delay: 20s;
        padding-top: 100px;
        color: var(--c-white);
    }

    top {
        padding-top: 0;
        color: var(--c-black-blue);
    }
}

.heroText__text {
    font-family: ManropeText;
    font-size: 24px;
    line-height: 36px;
    text-align: center;
    animation: heroTextLoad var(--heroText) ease-in-out;
}

@keyframes heroTextLoad {
    from, 50% {
        color: var(--c-white);
        padding-top: 50px;
    }

    to {
        color: var(--c-black-blue);
        padding-top: 0;
    }
}

.avatarsAndPhone {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 100%;
    margin-bottom: 80px;
}

.iphoneOleg {
    width: 512px;
    height: 540px;
    animation: iphoneOlegLoad var(--iphoneOlegLoad) ease-out forwards;
}

.avatars {
    position: absolute;
    width: 95%;
    height: 100%;
    animation: lalala 1.3s ease-out forwards;
    animation-delay: 3s;
    opacity: 0;
}

@keyframes lalala {
    from {
        width: 95%;
        opacity: 0;
        margin-top: 40px;
    }

     to {
         margin-top: 0;
        opacity: 1;
        width: 100%;
    }
}

@keyframes iphoneOlegLoad {
    from, 80% {
        margin-top: 40px;
        opacity: 0;
    }

    to {
        margin: 0;
        opacity: 1;
    }
}

.ponytail {
    background: url("../../assets/img/faces/ponytail.png"), var(--c-cyan);
    left: 0;
    animation: Ponytail 5s ease-in-out infinite;
}

.bang {
    background: url("../../assets/img/faces/bang.png"), var(--c-pink);
    left: 152px;
    top: 191px;
    animation: Bang 5s ease-in-out infinite;
}

.ponytail, .bang {
    width: 200px;
    height: 200px;
    position: absolute;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    border-radius: 50%;
    background-repeat: no-repeat;
    background-size: 119px;
    background-position: bottom center;
}

.app__left, .ton__left {
    position: absolute;
    color: var(--c-white);
    font-family: Manrope;
    font-size: 16px;
    line-height: 16px;
    padding: 5px 8px 7px;
    border-radius: 6px; 
    transform: rotate(10deg); 
}

.app__left {
    background-color: var(--c-orange);
    top: 101px;
    left: 12px;
    animation: App__left 5s ease-in-out infinite;
}

.ton__left {
    background-color: var(--c-pacific-blue);
    top: 210px;
    left: 160px;
    animation: TON__left 5s ease-in-out infinite;
}



@keyframes Ponytail {
    from {
        top: 101px;
    }

    50% {
        top: 106px;
    }

    to {
        top: 101px;
    }
}

@keyframes Bang {
    from {
        top: 191px;
    }

    50% {
        top: 187px;
    }

    to {
        top: 191px;
    }
}

@keyframes App__left {
    from {
        top: 101px;
        transform: rotate(10deg);
    }

    50% {
        top: 106px;
        transform: rotate(15deg);
    }

    to {
        top: 101px;
        transform: rotate(10deg);
    }
}

@keyframes TON__left {
    from {
        top: 129px;
        transform: rotate(12deg);
    }

    50% {
        top: 125px;
        transform: rotate(7deg);
    }

    to {
        top: 129px;
        transform: rotate(12deg);
    }
}